input[type="checkbox"] {
  width: 20px;
  height: 20px;
}


.switch-container {
	position: absolute;
	top: 25%; left: 25%;
	-webkit-transform: translate3d( -25%, -25%, 0 );
	transform: translate3d( -25%, -25%, 0 );
}

input[type="checkbox"].switch  {
	position: absolute;
	opacity: 0;
}

input[type="checkbox"].switch + div {
	vertical-align: middle;
	width: 20px;
	height: 10px;
	border-radius: 35px;
	background-color: #999999;
	-webkit-transition-duration: .4s;
	-o-transition-duration: .4s;
	   transition-duration: .4s;
	-webkit-transition-property: background-color, box-shadow;
	-webkit-transition-property: background-color, -webkit-box-shadow;
	transition-property: background-color, -webkit-box-shadow;
	-o-transition-property: background-color, box-shadow;
	transition-property: background-color, box-shadow;
	transition-property: background-color, box-shadow, -webkit-box-shadow;
	cursor: pointer;
}

input[type="checkbox"].switch + div span {
	position: absolute;
    font-size: 1.6rem;
    color: white;
    margin-top: 6px;
}

input[type="checkbox"].switch + div span:nth-child( 1 ) {
    margin-left: 8px;
}

input[type="checkbox"].switch + div span:nth-child( 2 ) {
    margin-left: 28px;
}

input[type="checkbox"].switch:checked + div {
	width: 47px;
	background-position: 0 0;
	background-color: #999999;
}

input[type="checkbox"].switch + div {
	width: 47px;
	height: 24px;
}


input[type="checkbox"].switch:checked + div {
	background-color: #337840;
}

input[type="checkbox"].switch + div > div {
	float: left;
	width: 23px; height: 22px;
	border-radius: inherit;
	background: #ffffff;
	-webkit-transition-timing-function: cubic-bezier(1,0,0,1);
	-o-transition-timing-function: cubic-bezier(1,0,0,1);
	   transition-timing-function: cubic-bezier(1,0,0,1);
	-webkit-transition-duration: 0.4s;
	-o-transition-duration: 0.4s;
	   transition-duration: 0.4s;
	-webkit-transition-property: transform, background-color;
	-webkit-transition-property: background-color, -webkit-transform;
	transition-property: background-color, -webkit-transform;
	-o-transition-property: transform, background-color;
	transition-property: transform, background-color;
	transition-property: transform, background-color, -webkit-transform;
	pointer-events: none;
	margin-top: 1px;
	margin-left: 1px;
}

input[type="checkbox"].switch:checked + div > div {
	-webkit-transform: translate3d(10px, 0, 0);
	transform: translate3d(10px, 0, 0);
	background-color: #ffffff;
}

input[type="checkbox"].bigswitch.switch + div > div {
	width: 22px;
	height: 22px;
	margin-top: 1px;
}


input[type="checkbox"].switch:checked + div > div {
	-webkit-transform: translate3d(23px, 0, 0);
	transform: translate3d(23px, 0, 0);
}

