.search-box{
  display: block;
  position: absolute;
  top: 50px;
  z-index: 99999999!important;
  max-height: 400px;
  overflow-y: auto;  
  -webkit-transition-property: top,bottom;
  -o-transition-property: top,bottom;
  transition-property: top,bottom;
  -webkit-transition-duration: .2s,.2s;
  -o-transition-duration: .2s,.2s;
  transition-duration: .2s,.2s;
  -webkit-transition-timing-function: linear,linear;
  -o-transition-timing-function: linear,linear;
  transition-timing-function: linear,linear;
  -webkit-overflow-scrolling: touch;
}
.search-box::-webkit-scrollbar, body::-webkit-scrollbar {
  width: 0.7em;
}
.search-box::-webkit-scrollbar-thumb, body::-webkit-scrollbar-thumb {
  background-color: #b67510;
  outline: 1px solid slategrey;
}
.search-box > ul > li:first-child {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.search-box > ul > li {
  cursor: pointer!important;
  min-width: 190px!important;
  width:300px;
  background-color: ghostwhite;
  padding: 10px 20px;
  border: 1px solid #cacaca96;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.search-box > ul > li > div > h5 {
  position: absolute;
  top: 8px;
  right: 6px;
  font-size:12px;
}
.search-box > ul > li > div > p {
  font-size: 13px;
  margin-right: 150px;
}
.search-box > ul > li:not(:first-child) {
  border-top: 0;
}
.search-box > ul > li:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.search-box > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  min-width: 190px!important;
}
