body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.sidebar.disabled{
  opacity: 0.4;
  z-index: 999;
  background-color: transparent;
}

.bread-crumb{
  font-size:20px;
  text-transform: uppercase
}

label {
  cursor: pointer;
  padding-top: 5px;
}

.navbar-default .navbar-toggle .icon-bar {
  background-color: #fff;
}
.main-panel{
  overflow:visible;
}
.main-panel .navbar {
  margin-bottom: 0;
  position: relative;
  top: 0;
  /* width: 100%; */
  z-index: 1049;
}

@media (max-width:767px){
  .main-panel .navbar {
      margin-bottom: 0;
      position: fixed;
      top: 0;
      /* width: inherit; */
      z-index: 1049;
  }
  .modal.in .modal-dialog {
    margin-top: 120px !important;
  }
  .main-panel > .content {
    margin-top: 100px;
  }
}

